import React from 'react'
import FancyCard from './ui/FancyCard';

export default function Tools() {
    return (
        <div className="tools">
            <FancyCard title="WordPress" image="assets/icons/wordpress.png" css="background-image: linear-gradient(to bottom, #0c3483 0%, #a2b6df 100%, #6b8cce 100%, #a2b6df 100%);"/>
            <FancyCard title="Photoshop CC" image="assets/icons/photoshop.png" css="background-image: linear-gradient( 65.5deg,  rgba(23,205,205,1) -15.1%, rgba(23,25,95,1) 71.5% );"/>
            <FancyCard title="Adobe XD" image="assets/icons/xd.png" css="color: #010101; background-image: linear-gradient( 109.6deg,  rgba(185,212,242,1) 11.2%, rgba(244,210,226,1) 100.3% );"/>
            <FancyCard title="npm & yarn" image="assets/icons/npm.png" css="background-image: radial-gradient( circle farthest-corner at 50.4% 50.5%,  rgba(251,32,86,1) 0%, rgba(135,2,35,1) 90% );"/>
            <FancyCard title="UI & UX Design" image="assets/icons/uiux.png" css="background-image: linear-gradient(to bottom, #29323c 0%, #485563 100%);"/>
            <FancyCard title="Jquery & Bootstrap" image="assets/icons/jb.png" css="color: #010101;background-image: linear-gradient(-225deg, #DFFFCD 0%, #90F9C4 48%, #39F3BB 100%);"/>
            <FancyCard title="Sass & Less" image="assets/icons/sass.png" css="background-image: linear-gradient( to top,  rgba(219,76,180,1), rgba(231,119,209,1), rgba(255,180,241,1) );"/>
            <style jsx>{`
            .tools{
                padding: 0 5%;
                display: flex;
                flex-direction: rows;
                flex-wrap: wrap;
                justify-content: spece-evenly;
            }
            `}</style>
        </div>
    )
}
